  }
  &:hover {
    color: #fff;
    background-color: #3C98E8;
    i {
      color: #fff;
    }
  }
}

.file-list {
  .file-item:first-child {
    margin-top: 12px;
  }
}

.file-item {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 14px;
  .file-name {
    font-size: 14px;
    color: #35383A;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
