    &>div {
      white-space: nowrap;
      text-overflow: ellipsis;
      padding-right: 20px;
      overflow: hidden;
    }
  }
  .file-click {
    display: flex;
    flex-shrink: 0;
    font-size: 14px;
    cursor: pointer;
    margin: 0px 10px;
    position: relative;
    overflow: hidden;
    &>input {
      position: absolute;
      left: 0px;
      right: 0px;
      top: 0px;
      bottom: 0px;
      opacity: 0;
      cursor: pointer;
    }
    &:hover {
      color: #3C98E8;
    }
    &.file-click-del:hover {
      color: #F01400;
    }
  }
}

.content-confirm-btn {
  line-height: 40px;
  width: 140px;
  text-align: center;
  &.btn-blue-border {
    line-height: 38px;
    margin-right: 20px;
  }
}

.form-editorbox {
  .form-input {
    width: 750px;
    height: 300px;
  }
}

.content-confirm {
  margin-left: 198px;
}

@media (max-width: 1350px) {
  .form-editorbox .form-input {
    width: 490px;
  }
}

</style>



